<template>
  <div class="container">
    <h1 class="mb-3">Notas</h1>
    <router-link :to="{ name: 'Crear' }" class="btn btn-primary"
      >Crear</router-link
    >
    <table class="table table-striped">
      <thead>
        <tr>
          <th>Nombre</th>
          <th>Descripcion</th>
          <th>Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(nota, index) in notas" :key="index">
          <td>{{ nota.nombre }}</td>
          <td>{{ nota.descripcion }}</td>
          <td>
            <router-link
              :to="{ name: 'Editar', params: { id: nota._id } }"
              class="btn btn-dark"
              >Editar</router-link
            >
            <a @click="eliminarNota(nota._id)" class="btn btn-danger ml-2"
              >Eliminar</a
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: "Notas",
  computed: {
    ...mapState(['token'])
  },
  data() {
    return {
      notas: [],
    };
  },
  methods: {
    listarNotas() {
      this.axios
        .get("/notas", {
          headers: {
            token: this.token
          }
        })
        .then((res) => {
          this.notas = res.data;
        })
        .catch((err) => console.log(err));
    },
    eliminarNota(id) {
      this.$swal({
        title: "Desea eliminar el registro?",
        text: "No se podra recuperar!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.isConfirmed) {
          this.axios
            .delete(`notas/${id}/eliminar`)
            .then((res) => {
              const buscarId = this.notas.findIndex((item) => item._id === id);
              this.notas.splice(buscarId, 1);
              this.$swal("Deleted!", "", "success");
            })
            .catch((err) => console.log(err));
        }
      });
    },
  },
  created() {
    this.listarNotas();
  },
};
</script>